.LoginTitle {
  text-align: center;
  font-size: 50px;
}
.FormBack {
  font-family: "DoHeyon";
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.inputEmail,
.inputPass {
  width: 300px;
  height: 50px;
  border-radius: 5px;
  box-shadow: none;
  border: 1px solid black;
  margin-top: 8px;
  padding-left: 12px;
  font-size: 20px;
  font-weight: bold;
}
input:focus {
  outline: none;
}
input::placeholder {
  font-weight: bold;
  font-size: 20px;
}
.btnLogin {
  width: 300px;
  height: 50px;
  background: #1890ff;
  border-radius: 5px;
  box-shadow: none;
  color: white;
}
button:focus {
  outline: none;
}
