.profileEdit {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 1px solid;
}
.profileLabel {
  width: 300px;
  height: 50px;
  display: flex;
  border-radius: 5px;
  align-items: center;
  box-shadow: none;
  border: 1px solid black;
  margin-top: 8px;
  padding-left: 12px;
  font-size: 20px;
  font-weight: bold;
}
